@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
	background-color: v.$primario;
	padding-top: 5rem;
	margin-top: 5rem;

	&__grid {
		@include m.tablet {
			@include m.grid(3, 2rem);
		}
	}

	&__box {
		margin-bottom: 5rem;

		@include m.tablet {
			margin-bottom: 0;
		}
	}

	&__heading {
		color: v.$blanco;
		text-transform: uppercase;
		text-align: center;
		font-size: 2rem;

		@include m.tablet {
			text-align: start;
		}
	}

	&__texto {
		color: v.$blanco;
		text-align: center;

		@include m.tablet {
			text-align: start;
			width: 28rem;
		}
		@include m.desktopXL {
			width: 35rem;
		}
	}

	&__navegacion {
		display: flex;
		flex-direction: column;
		align-items: center;

		@include m.tablet {
			align-items: start;
		}
	}

	&__link {
		color: v.$blanco;
		font-size: 1.8rem;
		margin-bottom: 1rem;
		transition-property: color;
		transition-duration: 300ms;

		&:last-of-type {
			margin-bottom: 0;
		}

		&:hover {
			color: lighten(v.$primario, 50%);
		}
	}

	&__copyright {
		background-color: darken(v.$primario, 10%);
		text-align: center;
		padding: 1rem;
		margin: 5rem 0 0 0;
		color: v.$blanco;
	}
}